.App {
  text-align: center;
  height: 100vh;
  width: 100%;
}

.TopMenu{

  width: 100%;
  height: 20%;
  background-color: cornflowerblue;

}

.SelectorMain{
  margin-top: 3%;
  text-align: center;
  height: 50%;
  width: 50%;
  display: inline-block;
  
  border-radius: 40px;
  
}

.ChooseButton{

  background-color: cornflowerblue;
  border: 1px solid white;
  color: whitesmoke;
  width: 100px;
  height: 50px;
  font-weight: bold;
  border-radius: 5px;

}


